.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #584f5d;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ebe8ec;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.sortButton {
  background-color: #584f5d;
  color:#ebe8ec;
  border: 5px solid #ebe8ec;

  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 50px;
  cursor:pointer;
  margin-bottom: 5px;
}

.sortButton:hover {
  background-color: #ebe8ec;
  color:#584f5d;
}

